/* ================== Root ====================== */
body {
  min-height: 100vh;
  margin: 0;
  padding: 0;
  width: 100vw;
  overflow-x: hidden;
  background: -webkit-gradient(linear, left top, right top, from(#1b2d50), to(#0a0111));
  background: -webkit-linear-gradient(left, #1b2d50 0%, #0a0111 100%);
  background: linear-gradient(90deg, #1b2d50 0%, #0a0111 100%);
  background-repeat: no-repeat;
  background-attachment: fixed;
  color: #cdcdcd;
  font-size: 1.33rem;
}

@media (min-width: 1200px) {
  body {
    /* for scrollbar*/
    margin-left: 0px; /*25px*/
    float: left;
    margin-bottom: 25px;
  }
  #thin::-webkit-scrollbar {
    width: 10px;
    background-color: #f5f5f5;
  }

  #thin::-webkit-scrollbar-track {
    -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
    background-color: #0a0111;
    border-radius: 10px;
  }

  #thin::-webkit-scrollbar-thumb {
    border-radius: 10px;
    background-image: -webkit-gradient(
      linear,
      left bottom,
      left top,
      color-stop(0.44, cyan),
      color-stop(0.72, white),
      color-stop(0.86, #05070b)
    );
  }
}

@media (max-width: 800px) {
  html,
  body {
    position: relative;
    overflow-x: hidden;
  }
}

